.todo-list-template{
  background: white;
  width: 512px;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  margin: 0 auto;
  margin-top: 4rem;
}

.title{
  padding: 2rem;
  font-size: 2.5rem;
  text-align: center;
  font-weight: 100;
  background: #22b8cf;
  color: white;
}

.form-wrapper{
  padding: 1rem;
  border-bottom: 1px solid #22b8cf;
}

.todos-wrapper{
  min-height: 5rem;
}

.pallete-wrapper{
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 20px;
  background: #f3f3f3;
}
